import { Component, OnInit, QueryList, ViewChildren, ViewChild, ElementRef } from '@angular/core';
import { Subscribable, Subscriber, Observer, Subscription } from 'rxjs';
// import { type } from 'os';

@Component({
  selector: 'app-section-chat',
  templateUrl: './section-chat.component.html',
  styleUrls: ['./section-chat.component.scss']
})
export class SectionChatComponent implements OnInit {

  constructor() { }
  @ViewChildren('mensajes') things: QueryList<any>;
  @ViewChild('chatbody',{static:true}) chatBody : ElementRef;
  mensaje:{isMy:boolean,type:'string'|'File',content:any,date?:Date}=
    {isMy:true,type:'string',content:null};
  mensajes:{isMy:boolean,type:'string'|'File',content:any,date?:Date}[]=[
    {isMy:true,type:'string',content:'segovia es gay'},
    // {isMy:true,type:'string',content:'por 1'},
    // {isMy:false,type:'string',content:'Hola mundo'},
    // {isMy:true,type:'string',content:'Hola mundo'},
    // {isMy:true,type:'string',content:'Hola mundo Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe earum in eveniet! Sed odit veniam ad dolore vitae, similique repudiandae, laudantium quibusdam natus tempore cupiditate beatae quisquam ducimus, animi necessitatibus'},
    // {isMy:false,type:'string',content:'Hola mundo'},
    // {isMy:true,type:'string',content:'Hola mundo'},
    // {isMy:true,type:'string',content:'Hola mundo mmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmmm'},
    // {isMy:false,type:'string',content:'Hola mundo Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe earum in eveniet! Sed odit veniam ad dolore vitae, similique repudiandae, laudantium quibusdam natus tempore cupiditate beatae quisquam ducimus, animi necessitatibus'},
  ]
  btnIsValid:boolean = false;
  ngOnInit() {
 
  }
  chatSuscription:Subscription;
  isOpenChat:boolean = false;
  OpenOrCloseChat():void{
    this.isOpenChat = !this.isOpenChat;
    if(this.isOpenChat)
    {
      this.chatBody.nativeElement.scrollTop = this.chatBody.nativeElement.scrollHeight;
      this.chatSuscription = this.things.changes.subscribe(t => {
         // this.chatAbajo();
         this.chatBody.nativeElement.scrollTop = this.chatBody.nativeElement.scrollHeight;
         console.log('chat abierto'); 
       })
    }
    else{
      this.chatSuscription.unsubscribe();
      console.log('chat cerrado');  
    }
  }
  
  keyUpSms():void
  {
    if(this.mensaje.content!=null && this.mensaje.content.trim()!= "")
    {
      this.btnIsValid = true;
    }
    else this.btnIsValid = false;
  }

  sendMessage():void
  {
    switch (this.mensaje.type) {
      case 'string':
        if(this.mensaje.content!=null && this.mensaje.content.trim()!= "")
        {
          let newMensaje = Object.assign({},this.mensaje);
          this.mensajes.push(newMensaje);
          this.mensaje.content=null;
          this.btnIsValid = false;
        
        }
        break;
     case 'File':
        if(this.mensaje.content!=null && this.mensaje.content.trim()!= "")
        {
          this.mensajes.push(this.mensaje);
          this.btnIsValid = false;
          this.mensaje.content=null;
        }
      default:
        break;
    }
  }
  sendMessageFile()
  {

  }
 
}
